					'https://ykhyyy.oss-cn-beijing.aliyuncs.com/MyApp/userAvatar/default-avatar.png'
				],
				tags: ['校园生活', '美食分享', '日常'],
				views: 128,
				likes: 32,
				comments: [
					{
						username: '评论者1',
						avatar: 'https://ykhyyy.oss-cn-beijing.aliyuncs.com/MyApp/userAvatar/default-avatar.png',
						content: '这是一条评论内容',
						time: '昨天 18:30',
						likes: 3
					},
					{
						username: '评论者2',
						avatar: 'https://ykhyyy.oss-cn-beijing.aliyuncs.com/MyApp/userAvatar/default-avatar.png',
						content: '好喜欢这个帖子的内容，非常实用！',
						time: '昨天 19:45',
						likes: 5
					}
				]
			}
		}
	},
	onLoad(options) {
		if (options.id) {
			this.id = options.id;
			this.loadPostDetails();
		}
	},
	methods: {
		// 加载帖子详情
		loadPostDetails() {
			// 实际应用中应该从API获取数据
			// 这里使用mock数据
			console.log(`Loading post details for ID: ${this.id}`);
		},
		
		// 提交评论
		submitComment() {
			if (!this.commentText.trim()) return;
			
			// 创建新评论
			const newComment = {
				username: '我',
				avatar: 'https://ykhyyy.oss-cn-beijing.aliyuncs.com/MyApp/userAvatar/default-avatar.png',
				content: this.commentText,
				time: '刚刚',
				likes: 0
			};
			
			// 添加到评论列表
			this.post.comments.push(newComment);
			
			// 清空输入框
			this.commentText = '';
			
			// 显示提示
			uni.showToast({
				title: '评论成功',
				icon: 'success'
			});
		}
	}
}
</script>

